import { Component } from "@angular/core";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import {
  AlertController,
  ModalController,
  Platform,
  ToastController,
} from "@ionic/angular";

import { User } from "../models/user";
import { Config, ConfigService } from "../services/config.service";
import { NavService } from "../services/navigation.service";
import { AuthService } from "./../services/auth.service";
import { ForgotPasswordModal } from "./forgot-password/forgot-password.modal";
import { GoogleAuthService } from "../services/google-auth.service";
import { I18nService } from "../services/i18n.service";

@Component({
  selector: "app-login",
  templateUrl: "./login.page.html",
  styleUrls: ["./login.page.scss"],
})
export class LoginPage {
  public loginForm: FormGroup;
  get usernameInput() {
    return this.loginForm.get("username");
  }
  get passwordInput() {
    return this.loginForm.get("password");
  }

  public insertPassword = false;
  public clearPassword = false;
  public activeView: "login" | "register" = "login";
  private returnUrl: string;
  public username: string;
  public password: string;
  public email: string;
  public provider: "google";
  public credential: string;
  public language: string;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private auth: AuthService,
    private alert: AlertController,
    private modalController: ModalController,
    private toast: ToastController,
    public fb: FormBuilder,
    private nav: NavService,
    public platform: Platform,
    private config: ConfigService,
    private googleAuth: GoogleAuthService,
    private i18n: I18nService
  ) {
    if (localStorage.getItem("currentUser")) {
      this.router.navigate(["/"]);
    }

    this.loginForm = fb.group({
      username: new FormControl("", [
        Validators.required,
        Validators.minLength(3),
      ]),
      password: new FormControl("", [
        this.insertPassword ? Validators.required : Validators.nullValidator,
        Validators.minLength(8),
      ]),
    });
  }

  async ngOnInit() {
    this.returnUrl = this.route.snapshot.queryParams["returnUrl"] || "/";
    this.language = (await this.config.get("language")) as Config["language"];
  }

  async checkLogin() {
    if (!this.insertPassword && this.usernameInput.valid) {
      try {
        await this.auth.checkLogin(this.usernameInput.value);
        this.insertPassword = true;
        return;
      } catch (e) {
        if (this.usernameInput.value.includes("@")) {
          this.email = this.usernameInput.value;
        } else {
          this.username = this.usernameInput.value;
        }
        this.showRegister();
        return;
      }
    }
    this.showRegister();
  }

  async submitLogin() {
    if (this.loginForm.valid) {
      try {
        const user = await this.auth.login(
          this.usernameInput.value,
          this.passwordInput.value
        );
        this.loginSuccess(user);
      } catch (e) {
        if (e.message) {
          this.loginError(e.message);
        } else if (e.detail) {
          this.loginError(e.detail);
        } else {
          this.loginError(e);
        }
      }
    }
  }

  async loginWithGoogle() {
    try {
      await this.googleAuth.logout();
    } catch (e) {
      console.error(e);
    }
    try {
      const googleUser = await this.googleAuth.signIn();
      if (googleUser.authentication.idToken) {
        this.credential = googleUser.authentication.idToken;
        this.provider = "google";
        this.username =
          googleUser.name ?? `${googleUser.givenName} ${googleUser.familyName}`;
        this.email = googleUser.email;

        try {
          await this.auth.checkLogin(this.email);
          try {
            const user = await this.auth.loginWithProvider(
              this.provider,
              this.credential
            );
            this.loginSuccess(user);
          } catch (e) {
            this.insertPassword = true;
          }
        } catch (e) {
          this.showRegister();
          return;
        }
      }
    } catch (error) {
      console.error(error);
      if (
        error.toString().includes("popup_closed_by_user") ||
        error.toString().includes("12501")
      ) {
        const alert = await this.alert.create({
          header: this.i18n.translate("google-sign-in-error"),
          message: this.i18n.translate("google-sign-in-failed"),
          buttons: [this.i18n.translate("oki-doki")],
          cssClass: "round-alert",
        });

        await alert.present();
      }
    }
  }

  async loginSuccess(user: User) {
    this.loginForm.reset();
    this.config.getConfig(true);
    if (user && user.two_step) {
      this.nav.navigateRoot(["/login/two-step"], {
        queryParams: { returnUrl: this.returnUrl },
      });
    } else {
      this.auth.setAuthUser(user);
      (
        await this.toast.create({
          message: "¡Acceso concedido!",
          duration: 2000,
          position: "bottom",
        })
      ).present();
      this.nav.navigateRoot(this.returnUrl);
    }
    await this.i18n.setLanguage(user.language);
  }

  async loginError(message: string) {
    let header = "";
    if (message.includes("Invalid credentials.")) {
      this.showRegister();
      header = this.i18n.translate("authentication-error");
      message = this.i18n.translate("incorrect-username-or-password");
    } else if (message.includes("Banned account.")) {
      header = this.i18n.translate("you-are-banned");
      const data = JSON.parse(message);
      const date = data.end?.date;

      message =
        this.i18n.translate("reason") +
        data.reason +
        "." +
        "<br />" +
        this.i18n.translate("end-of-ban") +
        (date ? date.split(" ")[0] : this.i18n.translate("indefinite"));
    } else if (message === "Internal Server Error" || message === "OK") {
      header = this.i18n.translate("system-error");
      message = this.i18n.translate("write-us-error");
    } else {
      header = this.i18n.translate("unknown-error");
      message = this.i18n.translate("write-us-error");
    }

    const alert = await this.alert.create({
      header,
      message,
      buttons: [this.i18n.translate("oki-doki")],
      cssClass: "round-alert",
    });

    await alert.present();
  }

  async forgotPassword() {
    const modal = await this.modalController.create({
      component: ForgotPasswordModal,
      componentProps: { username: this.username },
    });
    return await modal.present();
  }

  backToLogin() {
    this.activeView = "login";
  }

  showRegister() {
    this.activeView = "register";
  }

  async changeLanguage() {
    if (this.language === "es") {
      this.language = "en";
    } else {
      this.language = "es";
    }
    await this.i18n.setLanguage(this.language);
  }
}
